.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.score-container p {
  text-align: center;
  margin: 0px;
  font-size: 25px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.button-container {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

.button-click {
  width: 200px;
  height: 200px;
  border-radius: 150px;
  background-color: transparent;
  border: none;
  box-shadow: 0px 0px 10px 2px #cdcdcd;
}

.button-click h1 {
  margin: 0px;
  margin-top: 15px;
}

.button-click span {
  font-size: 10px;
  color: grey;
}

.button-click:active {
  box-shadow: inset 0px 0px 10px 5px #cdcdcd;
}
